.blackAndWhiteCollection10 {
  position: relative;
  width: 23.27px;
  height: 48.68px;
  flex-shrink: 0;
}
.logo {
  position: relative;
  line-height: 130%;
  display: none;
}
.logoExample {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-4xl);
}
.listItem {
  position: relative;
  line-height: 130%;
}
.navListItem {
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xs);
  align-items: flex-start;
  justify-content: flex-start;
}
.primaryButton {
  border-radius: var(--br-10xs);
  background-color: var(--primary-color);
  padding: var(--padding-base) var(--padding-31xl);
  justify-content: flex-start;
  color: var(--white);
}
.navMenu,
.navNavHeader1,
.primaryButton {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.navMenu {
  justify-content: flex-start;
  gap: var(--gap-xl);
  font-size: var(--font-size-sm);
  color: var(--grey-3);
  font-family: var(--font-poppins);
}
.navNavHeader1 {
  position: absolute;
  top: 28px;
  left: 0;
  background-color: var(--white);
  width: 1440px;
  padding: var(--padding-11xl) var(--padding-81xl);
  box-sizing: border-box;
  justify-content: space-between;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-black);
  font-family: var(--font-manrope);
}
