@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-poppins: Poppins;
  --font-manrope: Manrope;

  /* font sizes */
  --font-size-base: 16px;
  --font-size-5xl: 24px;
  --font-size-sm: 14px;
  --font-size-lg: 18px;

  /* Colors */
  --white: #fff;
  --grey-2: #bebebe;
  --text-black: #1b1b1b;
  --primary-color: #0c111a;
  --grey-3: #5c5c5c;
  --color-black: #000;
  --grey-1: #e5e7eb;

  /* Gaps */
  --gap-4xl: 23px;
  --gap-xl: 20px;
  --gap-3xs: 10px;

  /* Paddings */
  --padding-3xs: 10px;
  --padding-xl: 20px;
  --padding-51xl: 70px;
  --padding-11xl: 30px;
  --padding-81xl: 100px;
  --padding-base: 16px;
  --padding-31xl: 50px;
  --padding-smi: 13px;
  --padding-8xl: 27px;

  /* border radiuses */
  --br-9xs: 4px;
  --br-10xs: 3px;
  --br-11xs: 2px;
}
